import { Component, OnInit, TemplateRef, ElementRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { LanguageService } from '../../services/language.service';
import { Subscription } from 'rxjs';

import { ProductsService } from './products.service';
import { StoresService } from '../stores/stores.service';
import { InputSelectItem } from 'src/app/models/InputSelectItem';

import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-products',
  templateUrl: './products.component.html',
  styleUrls: ['./products.component.scss'],
})
export class ProductsComponent implements OnInit {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  private lastPageSub: Subscription;
  private newPagination;
  private lastPagination;

  private langSub: Subscription;
  currentLang;
  arabic: boolean;

  constructor(
    private productsService: ProductsService,
    private storesService: StoresService,
    private languageService: LanguageService,
    private modalService: BsModalService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private fb: FormBuilder,
    private router: Router,
    private elRef: ElementRef,
    private exportAsService: ExportAsService
  ) {
    this.setNow();

    this.langSub = this.languageService.currentLanguage.subscribe((lang) => {
      this.currentLang = lang;
      if (this.currentLang && this.currentLang === 'ar') {
        this.arabic = true;
      } else {
        this.arabic = false;
      }
    });

    this.lastPageSub = this.productsService.lastParsedPage.subscribe((data) => {
      if (data) {
        if (!this.newPagination && data) {
          this.newPagination = data;
        }
      }
    });
  }

  ngOnDestroy(): void {
    this.langSub.unsubscribe();
    this.lastPageSub.unsubscribe();
  }

  ngOnInit(): void {
    this.spinner.show();

    this.productsService.getAll();

    this.productsService.products.subscribe(async (data) => {
      if (data) {
        this.items = data;

        data.sort((a, b) => (a.active === b.active ? 0 : !a.active ? -1 : 1));

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();

        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });

        if (this.newPagination) {
          let nextBtn = this.elRef.nativeElement.querySelector(
            'button.btn.btn-default.pagination-nextpage'
          );
          let previousBtn = this.elRef.nativeElement.querySelector(
            'button.btn.btn-default.pagination-prevpage'
          );

          let clicks = this.newPagination.offset / 20;
          for (let index = 0; index < clicks; index++) {
            nextBtn.click();
          }
          return previousBtn.click();
        }
      }
    });

    this.spinner.hide();
  }

  savePagination() {
    this.productsService.pagination(this.lastPagination);
  }

  async reloadItems(event) {
    this.lastPagination = event;

    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.name.toLowerCase().includes(FilterRecord) ||
          item.category?.en.toLowerCase().includes(FilterRecord) ||
          item.creator?.name.toLowerCase().includes(FilterRecord) ||
          item.store?.name.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  // Start select dropdown
  selectStoresList: InputSelectItem[];
  selectedStoreId: string;
  getStoresList() {
    this.spinner.show();

    this.storesService.getAll();

    this.storesService.stores.subscribe((data) => {
      if (data) {
        this.selectStoresList = data.map((x) => {
          return {
            _id: x['_id'],
            nameEn: `${x['name']}`,
            nameAr: ``,
          };
        });
      }
    });

    this.spinner.hide();
  }
  selectStore(event) {
    if (event) {
      this.selectedStoreId = event._id;
    }
  }
  // End select dropdown

  addProductToStore() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService
      .addProductToStore(this.itemId, this.selectedStoreId)
      .subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.productsService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  discountForm = this.fb.group({
    discountType: [''],
    discountValue: [0],
    discountPercentage: [0],
    validTill: [],
  });

  minDate = new Date();
  datePickerValue: Date = new Date();
  time: string;
  forPeriod = false;
  setNow() {
    let now = new Date();
    let hours = ('0' + now.getHours()).slice(-2);
    let minutes = ('0' + now.getMinutes()).slice(-2);
    let str = hours + ':' + minutes;
    this.time = str;
  }
  changePeriod(event) {
    if (!event.target.checked) {
      this.discountForm.get('validTill').patchValue(undefined);
    }
  }

  copyElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.copyOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
        this.router.navigate([`./products/edit/${res['data']._id}`]);
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  addDiscount() {
    this.spinner.show();

    this.modalRef.hide();

    if (this.forPeriod) {
      var validTill = new Date(
        this.datePickerValue.getFullYear(),
        this.datePickerValue.getMonth(),
        this.datePickerValue.getDate(),
        parseInt(this.time.split(':')[0]),
        parseInt(this.time.split(':')[1]),
        0
      );

      this.discountForm.get('validTill').patchValue(validTill);
    }

    this.productsService
      .addDiscount(this.discountForm.value, this.itemId)
      .subscribe(
        (res) => {
          this.toastr.info(res['message'], 'Info');
          this.items = [];

          this.discountForm.reset();
          this.productsService.getAll();
        },
        (error) => {
          console.log(error);
          this.toastr.error(error.error.message, 'Error');
          // this.error.next(error.error.message);
        }
      );

    this.spinner.hide();
  }

  deleteDiscount() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.deleteDiscount(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deleteElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.deleteOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  activateElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.activateOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  deactivateElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.deactivateOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  soldElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.soldOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  availableElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.productsService.availableOne(this.itemId).subscribe(
      (res) => {
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.productsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService.save(this.exportAsConfig, 'Products').subscribe(() => {
      // save started
    });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
    });
  }
}
